const ADD_NEW_MESSAGE = 'dialogs/ADD_NEW_MESSAGE';

let initialState = {
    dialogs: [
        { id: 1, name: "Maxim" },
        { id: 2, name: "Dima" },
        { id: 3, name: "Sasha" },
        { id: 4, name: "Tatyana" },
        { id: 5, name: "Valera" }],

    messages: [
        { id: 1, text: "Hi!" },
        { id: 2, text: "What are you doing?" },
        { id: 3, text: "How are you?" },
        { id: 4, text: "Nice photo :)" },
        { id: 5, text: "Yo" }],
}

const dialogsReducer = (state = initialState, action) => {
    switch(action.type) {

        case ADD_NEW_MESSAGE:
            let newMessage = {id: 6, text: action.messageText};

            return {
                ...state,
                messages: [...state.messages, newMessage],
            };
        
        default:
            return state;
    }
}

export const addNewMessage = (messageText) => ({type: ADD_NEW_MESSAGE, messageText});

export default dialogsReducer;