import React from "react";
import classes from "./Users.module.css";
import undefinedProfilePicture from "../../assets/images/undefinedProfilePicture.png";
import { NavLink } from "react-router-dom";
import common from '../common/common.module.css'

let User = ({user, ...props}) => {
  // console.log('UPD USER')
  function isDisabled() {
    return props.isFollowing.some(id => id === user.id) || !props.authorizedUserID
  }

  let btnText = props.authorizedUserID ? 'Unfollow' : 'Follow' //  logout   users  

  return (
    <>
      <div className={classes.userItem} key={user.id}>
        <span>
          <div className={classes.userName}>{user.name}</div>

          <span className={classes.location}>
            <div>{"country"},</div>
            <div>{"city"}</div>
          </span>

          <div>
            <NavLink to={`/profile/${user.id}`}>
              <img
                src={user.photos.small != null ? user.photos.small : undefinedProfilePicture}
                className={classes.profilePicture + ' ' + common.uncopyable}/>
            </NavLink>
          </div>

          <span>
            <div>{user.status}</div>
          </span>
          <div>
            {user.followed 
            ? <button disabled={isDisabled()} onClick={() => {props.unfollow(user.id)}}>{btnText}</button> 
            : <button disabled={isDisabled()} onClick={() => {props.follow(user.id)}}>Follow</button>}
          </div>
        </span>
      </div>
    </>
  );
};

export default User;
