import React from "react";
import classes from "./ProfileInfo.module.css";
import undefinedProfilePicture from '../../../assets/images/undefinedProfilePicture.png'
import ProfileStatus from './ProfileStatus'

const ProfileInfo = (props) => {
  let profilePicture = null;

  if (!props.profile) {
    return
  }

  if (props.profile.photos.large === null) {
      profilePicture = undefinedProfilePicture;
  } else {
    profilePicture = props.profile.photos.large;
  }

  return (
      <div className={classes.description}>

        <img src={profilePicture} />

        <div className={classes.aboutMe}>
          <div className={classes.fullName}>
            <div className={classes.op}>
              [OP]
            </div>
            {props.profile.fullName}
          </div>
          
          <div>
            {props.profile.userId == props.authorizedUserID
            ? <ProfileStatus profile={props.profile} status={props.status} updateStatus={props.updateStatus}/>
            : (props.status ? props.status : '---')}
          </div>
        </div>

        <div>
        </div>
        
      </div>
  );
};

export default ProfileInfo;
