import React, { useEffect } from "react";
import Profile from "./Profile";

const ProfileWithHooks = (props) => {

  useEffect(() => {
    props.getProfile(props.userID);
    props.getStatus(props.userID);
    // console.log('useEffect from ProfileWithHooks' + Math.floor(Math.random() * 101))
  }, [props.userID])
  
  return <Profile {...props} />

}


export default ProfileWithHooks
