import { authMe } from "./auth-reducer";

const INITIALIZED_SUCCESFUL = 'app/INITIALIZED_SUCCESFUL';

let initialState = {
    initialized: false
}

const appReducer = (state = initialState, action) => {
    switch(action.type) {

        case INITIALIZED_SUCCESFUL:
            return {
                ...state,
                initialized: true
            }

        default:
            return state;
    };
}

export const initializedSuccesful = () => ({type: INITIALIZED_SUCCESFUL});

export const initialize = () => (dispatch) => {
    let promise = dispatch(authMe())
    Promise.all([promise])
    .then(() => {
        dispatch(initializedSuccesful());
    })
}

export default appReducer;