import React from "react";
import { connect } from "react-redux";
import {follow, getUsers, selectPage, unfollow} from "../../redux/reducers/users-reducer";
import Users from "./Users";
import { compose } from "redux";
import { getIsFetching, getIsFollowing, getPageSize, getSelectedPageNum, getTotalUsersCount, getUsersFromState } from "../../redux/selectors/users-selectors";

class UsersContainer extends React.Component {

  componentDidMount() {
    this.props.getUsers(this.props.selectedPage, this.props.pageSize);
  }

  onPageChanged = (pageNumberToSelect) => {
    this.props.selectPage(pageNumberToSelect);
    this.props.getUsers(pageNumberToSelect, this.props.pageSize);
  }

  render() {
    return (
        <Users {...this.props} onPageChanged={this.onPageChanged}/>
    );
  }
}

let mapStateToProps = (state) => {

  const usersPage = state.findUsersPage;

  return {
    users: getUsersFromState(state),
    pageSize: getPageSize(state),
    totalUsersCount: getTotalUsersCount(state),
    selectedPage: getSelectedPageNum(state),
    authorizedUserID: state.auth.id,
    isFetching: getIsFetching(state),
    isFollowing: getIsFollowing(state)
  };
};

export default compose(
  connect(mapStateToProps, {follow, unfollow, selectPage, getUsers})
)(UsersContainer);
