import common from '../common/common.module.css'
import React from 'react';
import classes from './Users.module.css'

export const Paginator = (props) => {
  let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize);
  let pages = [];

  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i);
  }

  function getPages() {
    let sPage = props.selectedPage;
    let offsetLeft = sPage < 6 ? 0 : sPage - 5;
    let offsetRight = sPage < 6 ? 9 : sPage + 4;
    let visiblePages = pages.slice(offsetLeft, offsetRight);
    return visiblePages;
  }

  return (
    <div className={classes.selectPageButton + " " + common.uncopyable}>
      {getPages().map((page) => {
        return (
          <span className={props.selectedPage === page && classes.selectedPage}
                onClick={() => {props.onPageChanged(page);}}>
            {page}{" "}
          </span>
        );
      })}
    </div>
  );
};
