import React from "react";
import classes from "./Dialogs.module.css";
import DialogItem from "./DialogItem/DialogItem";
import Message from "./Message/Message";
import { Field, reduxForm } from "redux-form";
import { required, maxLengthCreator } from "../../utils/validators/validators";
import { Textarea } from "../common/FormsControls/FormsControls";

const Dialogs = (props) => {

  let dialogsElements = props.dialogs.map((dialog) => (
    <DialogItem name={dialog.name} id={dialog.id} />
  ));

  let messages = props.messages.map((msg) => <Message text={msg.text} />);

  const onSubmit = (formData) => {
    props.addNewMessage(formData.newMessage);
  }

  return (
    <div className={classes.dialogs}>
      <div className={classes.dialogItems}>
        {dialogsElements}
      </div>
      <div className={classes.messages}>
        {messages}
      </div>

      <AddNewMessageReduxForm onSubmit={onSubmit}/>

    </div>
  );
};

const maxLength50 = maxLengthCreator(50);

const addNewMessageForm = (props) => {
  return(
    <form onSubmit={props.handleSubmit}>
      <Field component={Textarea} validate={[required, maxLength50]} 
      placeholder="Message" spellCheck='false' name="newMessage" />
      <button>Send message</button>
    </form>
  )
}

const AddNewMessageReduxForm = reduxForm({
  form: 'dialogAddMessage' //   
})(addNewMessageForm) 
  
export default Dialogs;
