import React from "react";
import classes from "./MyPosts.module.css";
import Post from "./Post/Posts";
import { Field, reduxForm } from "redux-form";
import { maxLengthCreator, required } from "../../../utils/validators/validators";
import { Textarea } from "../../common/FormsControls/FormsControls";

const MyPosts = React.memo(props => {
  let posts = props.posts.map((post) => (
    <Post text={post.text} likes={post.likes} comments={post.comments} />
  ));

  const onSubmit = (formData) => {
    props.addPost(formData.newPostText);
  }

  return (
    <>
      <div className={classes.new}>

      <div> <AddNewPostReduxForm onSubmit={onSubmit} /> </div>
      <div className={classes.postBlocks}> {posts} </div>

      </div>
    </>
  );
});

const maxLength500 = maxLengthCreator(500);

const addNewPostForm = (props) => {
  return(
    <form onSubmit={props.handleSubmit} className={classes.addNewPostForm}>
      <Field name="newPostText" component={Textarea} placeholder="Add a post..." spellCheck={false}
      validate={[required, maxLength500]}/>
      <button>Add post</button>
    </form>
  )
};

const AddNewPostReduxForm = reduxForm({
  form: 'addNewPost'
})(addNewPostForm)

export default MyPosts;
