import { usersAPI } from "../../API/usersAPI";
import { updateObjectInArray } from "../../utils/objects-helper";

const FOLLOW = 'users/FOLLOW';
const UN_FOLLOW = 'users/UN_FOLLOW';
const SET_USERS = 'users/SET_USERS';
const SELECT_PAGE = 'users/SELECT_PAGE';
const SET_TOTAL_USERS_COUNT = 'users/SET_TOTAL_USERS_COUNT';
const TOGGLE_IS_FETCHING = 'users/TOGGLE_IS_FETCHING';
const TOGGLE_IS_FOLLOWING = 'users/TOGGLE_IS_FOLLOWING';

let initialState = {
    users: [],
    pageSize: 3,
    totalUsersCount: 0,
    selectedPage: 1,
    isFetching: false,
    isFollowing: []
}

const findUsersReducer = (state = initialState, action) => {

    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userID, 'id', {followed: true})
            }

        case UN_FOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userID, 'id', {followed: false})
            }

        case SET_USERS:
            return { ...state, users: [...action.users] }

        case SELECT_PAGE:
            return { ...state, selectedPage: action.selectedPage }

        case SET_TOTAL_USERS_COUNT:
            return { ...state, totalUsersCount: action.totalUsersCount }

        case TOGGLE_IS_FETCHING:
            return { ...state, isFetching: action.isFetching }

        case TOGGLE_IS_FOLLOWING:
            return {
                ...state,
                isFollowing: action.isFollowing
                    ? [...state.isFollowing, action.userID]
                    : state.isFollowing.filter(id => id != action.userID)
            }

        default:
            return state;
    }
}

// action creators
export const followSuccesful = (userID) => ({ type: FOLLOW, userID });
export const unFollowSuccesful = (userID) => ({ type: UN_FOLLOW, userID });
export const setUsers = (users) => ({ type: SET_USERS, users });
export const selectPage = (selectedPage) => ({ type: SELECT_PAGE, selectedPage });
export const setTotalUsersCount = (totalUsersCount) => ({ type: SET_TOTAL_USERS_COUNT, totalUsersCount });
export const toggleIsFetching = (isFetching) => ({ type: TOGGLE_IS_FETCHING, isFetching });
export const toggleIsFollowing = (isFollowing, userID) => ({ type: TOGGLE_IS_FOLLOWING, isFollowing, userID });

// thunk creators
export const getUsers = (selectedPage, pageSize) => async (dispatch) => {
    dispatch(toggleIsFetching(true));
    let response = await usersAPI.getUsers(selectedPage, pageSize)
    dispatch(toggleIsFetching(false));
    dispatch(setUsers(response.items));
    dispatch(setTotalUsersCount(response.totalCount));
}

export const followUnfollowFlow = async (dispatch, userID, apiMethod, actionCreator) => {
    dispatch(toggleIsFollowing(true, userID));
    let response = await apiMethod(userID)
    console.log(response)
    response.resultCode === 0 && dispatch(actionCreator(userID))
    dispatch(toggleIsFollowing(false, userID));
}

export const unfollow = (userID) => async (dispatch) => {
    followUnfollowFlow(dispatch, userID, usersAPI.unfollow.bind(usersAPI), unFollowSuccesful);   
}

export const follow = (userID) => async (dispatch) => {
    followUnfollowFlow(dispatch, userID, usersAPI.follow.bind(usersAPI), followSuccesful);
}

export default findUsersReducer;
