import React from "react";
import classes from "./NavBar.module.css";
import { NavLink } from "react-router-dom";

const NavBar = (props) => {

  const setActive = ({isActive}) => isActive ? classes.active : classes.item

  return (
    <nav className={classes.nav}>
      <div><NavLink to={`/profile/${props.authorizedUserID}`} className={setActive}>Profile</NavLink></div> 
      <div><NavLink to="/dialogs" className={setActive}>Messages</NavLink></div>
      <div><NavLink to="/news" className={setActive}>News</NavLink></div>
      <div><NavLink to="/music" className={setActive}>Music</NavLink></div>
      <div><NavLink to="/users" className={setActive}>Find users</NavLink></div>
      <div><NavLink to="/settings" className={setActive}>Settings</NavLink></div>
    </nav>
  );
};

export default NavBar;
