import axios from "axios"

const instanse = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {"API-KEY": "599f3e35-0988-40c1-8b60-81a4fef8ca04"}
});

const getUsers = (selectedPage, pageSize) => {
    return (
        instanse.get(`users?page=${selectedPage}&count=${pageSize}`)
        .then(response => response.data)
    )
}

const unfollow = (userID) => {
    return (
        instanse.delete(`follow/${userID}`)
        .then(response => response.data)
    )
}

const follow = (userID) => {
    return (
        instanse.post(`follow/${userID}`)
        .then(response => response.data)
    )
}

const getProfile = (userID) => {
    return (
        instanse.get(`profile/${userID}`)
        .then(response => response.data)
    )
}

export const usersAPI = {
    getUsers,
    follow,
    unfollow,
    getProfile
}