import React from "react";
import classes from "./Post.module.css";

const Post = (props) => {
  return (
    <>
      <div className={classes.item}>
        <div>
          <img src="https://4x4photo.ru/wp-content/uploads/2023/05/70e6dbdc-1661-41c2-a033-179404ca8b9f.jpg" />
        </div>
        <span className={classes.postText}>{props.text}</span>
        <div>
          <span>
            <button className={classes.likes}>Like {props.likes}</button>
            <button className={classes.comments}>
              Comments {props.comments}
            </button>
            <button className={classes.comments}>Share</button>
          </span>
        </div>
      </div>
    </>
  );
};

export default Post;
