import React from "react"
import { Field, reduxForm } from "redux-form"
import { Input } from "../common/FormsControls/FormsControls"
import { required } from "../../utils/validators/validators"
import { connect } from "react-redux"
import { login } from "../../redux/reducers/auth-reducer"
import { Navigate } from "react-router-dom"
import classes from './Login.module.css'
import common from '../common/common.module.css'

const Login = (props) => {

    const onSubmit = (formData) => {
        props.login(formData.email, formData.password, formData.rememberMe)
    }

    if (props.isAuth) {
        return <Navigate to={`/profile/${props.authorizedUserID}`} />
    }

    return (
        <div className={classes.form}>
            <h1>LOGIN</h1>

            <LoginReduxForm onSubmit={onSubmit} />
        </div>
    )
}

const LoginForm = ({handleSubmit, error}) => {
    // e.preventDefault, get all from data and put them to obj, props.onSubmit(formData) 
    return(
        <form onSubmit={handleSubmit}>
            <div>
                <Field placeholder="Email" name="email" validate={[required]} component={Input} />
                <Field type="password" placeholder="Password" name="password" validate={[required]} component={Input} />
                <div className={classes.checkbox}>
                    <Field type="checkbox" name="rememberMe" component={Input} id="rememberMe"/>
                    <label className={common.uncopyable} htmlFor="rememberMe">Remember me</label>
                </div>
            </div>

            {error && <div className={classes.formError}>{error}</div>}

            <div>
                <button>Login</button>
            </div>
        </form>
    )
}

const LoginReduxForm = reduxForm({
    form: 'login' //   
})(LoginForm)

const mapStateToProps = (state) => {
    return {
        isAuth: state.auth.isAuth,
        authorizedUserID: state.auth.id
    }
}

export default connect(mapStateToProps, {login})(Login);